// define colour palett

$white: #fff;
$yellow: #DDA239;
$yellow-transp: #DDA23950;
$black: #000;
$grey: #3c3c3c;

// define font family
$body-font: 'Fira Sans', sans-serif;

html, body {
    background-color: $black;
    padding: 0;
    margin: 0;
    width: 100%;
    // max-width: 1200px;
    margin: 0 auto;
}

.dont-show {display:none;}
.centered {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

/* -----------------General Typography----------------- */
.text1, .text2, .text3, select, option, .author, .date, textarea,
label,
input,
h1, h2, h3, h4,
a, ul, li, p, td {
    font-family: $body-font;
    color: $yellow;
}

a {
    text-decoration: none;
}

a.underlined {
    text-decoration: underline;
}

a.underlined:hover {
    background-color: $yellow;
}

a.u-link {
    text-decoration: underline;
}

