#header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo {
    max-height: 100px;
    margin: 1% 3%;
}

.logo img {
    height: 100px;
    object-fit: contain;
}

div.nav-bar {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 3%;
}

.boka-bord-btn {
    background-color: $yellow;
    padding: 0 1%;
    line-height: 1em;
    border-radius: 10%;
    width: 140px;
}

.boka-bord-btn h2 {
    color: $black!important;
    text-align: center;
    font-size: 1.2em;
}

.boka-bord-btn:hover {
    background-color: $grey;
    color: $yellow!important;
}

.boka-bord-btn h2:hover {
    color: $yellow!important;
}

/* The Overlay (background) */
.overlay {
    /* Height & width depends on how you want to reveal the overlay (see JS below) */
    height: 100%;
    width: 0;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    right: 0;
    top: 0;
    background-color: #C4C4C4; 
    border-left: 1px solid #C4C4C4; 
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* Position the content inside the overlay */
.overlay-content {
    position: relative;
    top: 5%; /* 25% from the top */
    width: 100%; /* 100% width */
    text-align: left; /* Centered text/links */
    margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */
}

/* The navigation links inside the overlay */
.overlay a {
    font-size: 1.4em;
    line-height: 1.5em;
    padding: 8px 1em;
    text-decoration: none;
    color:$black!important;
    display: block; /* Display block instead of inline */
    transition: 0.3s; /* Transition effects on hover (color) */
}

/* When you mouse over the navigation links, change their color */
.icon:hover, .overlay a:hover, .overlay a:focus {
    background-color: $yellow;
    color:$grey!important;
}

.icon {
    color: $yellow;
    // border-radius: 50%;
    padding: 0.4em 0.7em;
    display: flex;
    justify-content: center;
    font-size: 20pt;
}



.closebtn {
    font-size: 1.5em;
    // border-radius: 50%;
}

/* Position the buttons (top right corner) */
.overlay .closebtn {
    position: absolute;
    /* top: 20px;
    right: 75px; */
    font-size: 2em!important;
}

// SECTION hero -------------------------------------------

section#hero {
    width: 100%;
    height: auto;
    max-height: 450px;
}

section#hero img {
    object-fit: cover;
    width: 100%;
    height: auto;
    max-height: 450px;
}

// SECTION choices --------------------------

section#choices {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3%;
    background: url('../img/background.jpg') no-repeat center fixed;     
    background-size: cover;
    background-color: $grey;
    width: 100%;
    height: 250px;
}

div.card {
    background-color: $white;
    width: 250px;
    border: 1px solid $yellow;
}

div.card:hover {
    background-color: $yellow;
}

div.card img {
    width: 250px;
    height: 125px;
    object-fit: cover;
}

div.card a h3 {
    color: black;
    text-align: center;
}         


// section insta -----------------------
section#insta {
    background-color: $yellow-transp;
    padding: 3% 0;
}

section#insta h2 {
    text-align: center;
    margin-top: 0;
}


div#insta-img {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1%;
    margin: 3% auto;
}

div#insta-img img {
    width: 240px;
    height: 180px;
    object-fit: cover;
    border: 1px solid $yellow;
}

// anm√§la nyheterna -----------------------------
section#newsletter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3%;
    background: url('../img/background.jpg') no-repeat center fixed;     
    background-size: cover;
    background-color: $grey;
    width: 100%;
    height: 250px;
}

section#newsletter div.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: $grey;
    padding: 2%;
