}

// div.col-left {}
div.col-left h3, div.col-left p { 
    text-align: left; 
    margin: 0;
    word-wrap: break-word;
}

div.col-left p { 
    font-size: 0.8em;
}

div.col-right {
    min-width: 200px;
    margin-left: auto;
    margin-right: auto;
}

div.col-right form {
    padding: 1%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
}
        
div.col-right form input {
    background-color: $yellow-transp;
    color: $white;
    text-align: left;
    padding: 2% 0 2% 5px;
    border: 0;
    margin: 1% auto;   
    width: 195px; 
}

div.col-right form input.submit {
    background-color: $yellow;
    color: $black;
    text-align: center;
    border: 1px solid transparent;
    width: 200px;
}

form input.submit:hover {
    background-color: $grey!important;
    border: 1px solid $yellow!important;
    color: $yellow!important;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: white;
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
    color: white;
  }
  


// footer --------------------------------
